function TayoriIframe() {
  const { i18n } = useTranslation();

  // For JP or unknown language, use original tayori form, no translation required.
  let src = 'https://tayori.com/f/mktoiawase/';

  if (i18n.language.includes('en')) {
    src = `tayori/en/`;
  } else if (i18n.language.includes('zh')) {
    src = `tayori/zh/`;
  }

  return (
    <Box
      sx={{
        pt: 8,
        width: '100vw',
        height: '2000px',
      }}
      id="questionary">
      <iframe
        src={src}
        title="tayoriQuestion"
        frameborder="0"
        width="100%"
        height="100%"
        marginheight="0"
        marginwidth="0"
        scrolling="no"></iframe>
    </Box>
  );
}

