// https://mui.com/material-ui/react-dialog/
// Adapted from react dialog example.

import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import Dialog from '@mui/material/Dialog';
import Button from '@mui/material/Button';
import ReactCountryFlag from 'react-country-flag';
import { useTranslation } from 'react-i18next';

function LanguageDialog(props) {
  const { onClose, selectedLanguage, open } = props;

  const handleClose = () => {
    onClose(selectedLanguage);
  };

  const handleListItemClick = (value) => {
    onClose(value);
  };

  const languages = ['jp', 'en', 'zh'];

  function getLanguageName(language) {
    switch (language) {
      case 'jp':
        return '';
      case 'en':
        return 'English';
      case 'zh':
        return '';
      default:
        return 'Unknown';
    }
  }

  function getCountryCode(language) {
    switch (language) {
      case 'jp':
        return 'JP';
      case 'en':
        return 'US';
      case 'zh':
        return 'CN';
      default:
        return 'Unknown';
    }
  }

  return (
    <Dialog onClose={handleClose} open={open}>
      <List sx={{ width: 200 }}>
        {languages.map((language) => (
          <ListItem disableGutters>
            <ListItemButton onClick={() => handleListItemClick(language)} key={language}>
              <ReactCountryFlag
                className="emojiFlag"
                countryCode={getCountryCode(language)}
                style={{
                  fontSize: '2em',
                  paddingRight: '0.5em',
                }}
                svg
              />
              <ListItemText primary={getLanguageName(language)} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
}

export default function LanguageButton() {
  const [languageDialogOpen, setLanguageDialogOpen] = React.useState(false);

  const { t, i18n } = useTranslation();

  const handleLanguageDialogOpen = () => {
    setLanguageDialogOpen(true);
  };

  const handleLanguageDialogClose = (language) => {
    setLanguageDialogOpen(false);
    i18n.changeLanguage(language);
  };

  return (
    <>
      <LanguageDialog
        selectedLanguage={i18n.language}
        open={languageDialogOpen}
        onClose={handleLanguageDialogClose}
      />
      <Button
        key={''}
        sx={{ my: 2, color: 'white', display: 'block' }}
        onClick={handleLanguageDialogOpen}>
         {t('language')}
      </Button>
    </>
  );
}
