import * as React from 'react';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import { AppBar, Toolbar, Slide } from '@mui/material';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import PropTypes from 'prop-types';
import { Link } from 'react-scroll';
import { useTranslation } from 'react-i18next';
import LanguageButton from './LanguageButton';

function HideOnScroll(props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
  });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

export default function ResponsiveAppBar() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  HideOnScroll.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
  };

  const { t } = useTranslation();

  return (
    <HideOnScroll>
      <AppBar elevation={0} sx={{ maxWidth: '100%' }}>
        <Container p={5}>
          <Toolbar disableGutters>
            <Typography
              variant="h6"
              noWrap
              component="div"
              sx={{ mr: 2, display: { xs: 'none', md: 'flex' } }}>
              {t('businessName')}
            </Typography>

            <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit">
                <MenuIcon />
              </IconButton>

              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'left',
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: 'block', md: 'none' },
                }}>
                <Link duration={1000} smooth={true} to="top">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('top')}
                  </MenuItem>
                </Link>

                <Link duration={1000} smooth={true} to="welcome">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('greeting')}
                  </MenuItem>
                </Link>

                <Link duration={1000} smooth={true} to="business">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('businessInfo')}
                  </MenuItem>
                </Link>

                <Link duration={1000} smooth={true} to="introduction">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('lawyerInfo')}
                  </MenuItem>
                </Link>

                <Link duration={1000} smooth={true} to="access">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('access')}
                  </MenuItem>
                </Link>

                <Link duration={1000} smooth={true} to="questionary">
                  <MenuItem key={''} onClick={() => handleCloseNavMenu()}>
                    {t('contact')}
                  </MenuItem>
                </Link>
              </Menu>
            </Box>

            <Typography
              variant="h6"
              noWrap
              component="div"
              sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
              {t('businessName')}
            </Typography>

            <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
              <Link duration={1000} smooth={true} to="top" sx={{ width: '100%', height: '100%' }}>
                <Button key={''} sx={{ my: 2, color: 'white', display: 'block' }}>
                  {t('top')}
                </Button>
              </Link>

              <Link duration={1000} smooth={true} to="welcome">
                <Button key={''} sx={{ my: 2, color: 'white', display: 'block' }}>
                  {t('greeting')}
                </Button>
              </Link>

              <Link duration={1000} smooth={true} to="business">
                <Button key={''} sx={{ my: 2, color: 'white', display: 'block' }}>
                  {t('businessInfo')}
                </Button>
              </Link>

              <Link duration={1000} smooth={true} to="introduction">
                <Button key={''} sx={{ my: 2, color: 'white', display: 'block' }}>
                  {t('lawyerInfo')}
                </Button>
              </Link>
              <Link duration={1000} smooth={true} to="access">
                <Button key={''} sx={{ my: 2, color: 'white', display: 'block' }}>
                  {t('access')}
                </Button>
              </Link>
            </Box>
            <LanguageButton />
            <Link duration={1000} smooth={true} to="questionary">
              <Button
                color="secondary"
                variant="contained"
                sx={{ color: 'black', display: { xs: 'none', md: 'block', borderRadius: 50 } }}
                pr={10}>
                {t('contact')}
              </Button>
            </Link>
          </Toolbar>
        </Container>
      </AppBar>
    </HideOnScroll>
  );
}
